  .gallery
    &-list
      display: flex
      flex-flow: row wrap
      &-item
        img
          max-width: 100%
          max-height: auto
    &-viewing
      z-index: 1000
      position: fixed
      top: 0
      left: 0
      right: 0
      bottom: 0
      background-color: $colors.charcoal
      display: flex
      flex-flow: column nowrap
      align-content: center
      align-items: center
      justify-content: center
      overflow: auto

      .close
        transition: $transition
        background-color: transparent
        border: none
        color: $colors.white
        font-size: 5rem
        position: fixed
        top: 0
        right: 0
        border: none
        outline: none
        &:hover
          color: $colors.blue

      &-item
        margin: auto
        margin: 0
        padding: 0
        text-align: center

        img
          box-shadow: 0 3px 9px darken($colors.charcoal, 50%)
          max-width: 100%
</style>
